.wrapper {
  left: 0%;
  width: 100%;
  margin: 0 auto;
  background-color: #141414;

  display: flex;
  flex-direction: column;
  align-self: end;
  margin-bottom: 10px;
}
.logoFooter {
  display: flex;
  flex-direction: row;
  gap: 20px;
  background-color: #141414;
  color: #141414;
  margin-left: 50px;
  margin-top: 40px;
}
.divider {
  background-color: #4d4d4d;
  height: 1px;
  width: 90%;
  margin-left: 50px;
}
.logoTitle {
  background-color: #141414;
  color: #666666;
  padding-bottom: 40px;
}
.svgFooter {
  background-color: #141414;
}
.footerNav {
  background-color: #141414;
  color: #ffffff;
  margin-left: 50px;
  margin-top: 40px;
  margin-bottom: 40px;
}
.mainTitle {
  background-color: #141414;
  color: #666666;
  margin-left: 50px;
  margin-top: 30px;
  font-family: Libre Franklin;
  font-style: Regular;
  font-size: 10px;
  line-height: 12px;
  line-height: 99%;

  vertical-align: Top;
  letter-spacing: 1px;
  fill: Solid;
}
.Nav {
  background-color: #141414;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 50px;
  grid-template-rows: 20px;
  font-family: Libre Franklin;
  font-style: Regular;
  font-size: 14px;
  line-height: 24px;
  line-height: 141%;
  color: #ffffff;
  vertical-align: Top;
  fill: Solid #ffffff;
  margin-bottom: 10px;
}
@media screen and (max-width: 1200px) {
  .Nav {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 30px;
    grid-template-rows: 20px;
  }
}
@media screen and (max-width: 980px) {
  .Nav {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    gap: 20px;
    grid-template-rows: 10px;
  }
}
@media screen and (max-width: 568px) {
  .Nav {
    grid-template-columns: 1fr;
    grid-template-rows: 1fr;
    gap: 20px;
    grid-template-rows: 10px;
  }
}
