.wrapper {
  width: 100%;
  margin: 0 auto;
}

.block {
  width: 100%;
  height: 100%;
  margin: 0 auto;

  background-color: #e5e5e5;
  display: flex;
  flex-direction: row;
  gap: 40px;
}
.block1 img {
  object-fit: cover;
  width: 100%;
}
.block2 {
  display: flex;
  flex-direction: column;
}
.title {
  font-family: Libre Franklin;
  font-style: Regular;
  font-size: 10px;
  line-height: 12px;
  line-break: 99%;

  letter-spacing: 1px;
  padding-bottom: 18px;
}
.bodyBig {
  font-family: Tenor Sans;
  font-style: Regular;
  font-size: 36px;
  line-height: 42px;
  line-height: 100%;
  text-align: Left;
  vertical-align: Top;
  text-transform: uppercase;
  padding-bottom: 27px;
}
.bodySmall {
  font-family: Source Serif Pro;
  font-style: Regular;
  font-size: 16px;
  line-height: 24px;
  line-height: 120%;
  text-align: Left;
  vertical-align: Top;
  padding-bottom: 28px;
}
.end {
  font-family: Libre Franklin;
  font-style: Bold;
  font-size: 10px;
  line-height: 12px;
  line-height: 99%;
  text-align: Left;
  vertical-align: Top;
  letter-spacing: 1px;
  text-transform: uppercase;
}
@media screen and(max-width : 1200px) {
  .block {
    flex-direction: column;
    align-items: center;
    gap: 30px;
  }
  .block1 {
    width: 80%;
  }
  .block2 {
    width: 80%;
    flex-direction: row;
    align-items: center;
    gap: 20px;
    margin-left: 20px;
    margin-bottom: 50px;
  }
}
@media screen and(max-width : 980px) {
  .wrapper {
    width: 80%;
  }
  .block {
    flex-direction: column;
    align-items: center;
    height: 800px;
  }
  .block1 {
    width: 100%;
  }
  .block2 {
    width: 100%;
    flex-direction: row;
    align-items: start;
    gap: 20px;
    margin-left: 20px;
  }
  .bodyBig {
    font-size: 30px;
  }
  .block1 img {
    width: 100%;
    margin-left: 10px;
    margin-right: 10px;
  }
}
