.container {
  margin: 0 auto;
  width: 70%;
}
.mainContent {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr 1fr;
  grid-template-rows: 1fr 1fr;
  gap: 0px 0px;
  grid-template-areas:
    '. . .'
    '. . .';
  justify-items: start;
  gap: 20px;
  margin-top: 20px;
  cursor: pointer;
  margin-bottom: 20px;
}
.newsBlock {
  max-width: 100%;
  display: flex;
  flex-direction: column;
}

.newsCard {
  max-width: 100%;
  display: flex;
  flex-direction: column;

  margin-left: 3%;
  margin-right: 3%;
}
.imgBlock {
  width: 100%;
  height: 100%;
}
.author {
  font-family: Libre Franklin;
  font-style: Bold;
  font-size: 10px;
  line-height: 12px;
  line-height: 99%;
  text-align: Left;
  vertical-align: Top;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding-top: 18px;
}
.title {
  font-family: Libre Franklin;
  font-style: Regular;
  font-size: 10px;
  line-height: 12px;
  line-height: 99%;
  text-align: Left;
  vertical-align: Top;
  letter-spacing: 1px;
  padding-top: 24px;
}
.body {
  font-family: Tenor Sans;
  font-style: Regular;
  font-size: 20px;
  line-height: 24px;
  line-height: 103%;
  text-align: Left;
  vertical-align: Top;
  padding-top: 18px;
}
@media screen and (max-width: 1200px) {
  .mainContent {
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas:
      '. . '
      '. . ';
    justify-items: start;
    gap: 15px;
  }
  .body {
    font-size: 30px;
  }
  .title {
    font-size: 1.2rem;
  }
  .author {
    font-size: 1rem;
    margin-bottom: 20px;
  }
  .container {
    width: 1000px;
  }
}
