@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,400;1,500;1,600;1,700;1,800;1,900&family=Tenor+Sans&display=swap');

html,
body,
.App,
.content {
  height: 100%;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #e5e5e5;
}

.App {
  height: 60%;
  margin-left: 5%;
  margin-right: 5%;
  display: flex;
  flex-direction: column;
}
.about {
  font-size: 22px;
  width: 100%;
  height: 100%;
}
@media screen and (max-width: 960) {
  .content {
    margin-left: 50px;
  }
}

.our_contacts {
  display: flex;
  flex-direction: column;
  font-size: 1.6rem;
  gap: 10px;
}
