@import url('https://fonts.googleapis.com/css2?family=Libre+Franklin:ital,wght@0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,200;1,400;1,500;1,600;1,700;1,800;1,900&display=swap');

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
.container {
  max-width: 1440px;
  max-height: 90px;
  background-color: #fff;
}

.logo {
  margin-top: 37px;
  margin-bottom: 37px;
}
.header {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.header__nav {
  display: flex;
  flex-direction: row;
  gap: 40px;
  padding-top: 33px;
  padding-bottom: 33px;
  margin-right: 30px;
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 1rem;
  line-height: 24px;
  color: white;
}
.link {
  list-style-type: none;
  text-decoration: none;
}
.active {
  text-decoration: underline;
}
@media screen and(max-width : 1200px) {
  .logo {
    flex: 1;
    margin-left: 10px;
  }
  .header__nav {
    font-size: 1.5rem;
  }
}

.active {
  text-decoration: none;
}
@media screen and(max-width : 980px) {
  .logo {
    flex: 1;
    margin-left: 10px;
  }
  .header__nav {
    font-size: 1.7rem;
  }
}

.active {
  text-decoration: none;
}
