.btn {
  font-family: 'Libre Franklin';
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  line-height: 12px;
  margin: 0 auto;
  align-self: center;
  letter-spacing: 1px;
  text-transform: uppercase;
  padding-top: 21px;
  padding-bottom: 21px;
  padding-left: 26px;
  padding-right: 26px;
  background: #e5e5e5;
  border: 1px solid #000000;
  border-radius: 2px;
  display: block;

  cursor: pointer;
  z-index: 1000;
}
.btn:hover {
  background-color: #fff;
}
